<template>
  <div class="blog-pagination">
    <button v-on:click="advance(-1)" class="next-prev"><icon name="chevron-left" class="icon"></icon></button>
    <button v-on:click="onClickButton(n)" v-for="n in pages" v-bind:class="{ selected: position === n }">{{n}}</button>
    <button v-on:click="advance(1)" class="next-prev"><icon name="chevron-right" class="icon"></icon></button>
  </div>
</template>

<script>
export default {
  name: 'blogpagination',
  props: ['total'],
  methods: {
    advance (direction) {
      if ((this.position + direction > 0) && (this.position + direction <= this.pages)) {
        this.onClickButton(this.position + direction)
      }
    },
    onClickButton (newPosition) {
      this.position = newPosition
      this.$emit('clicked', newPosition - 1)
    }
  },
  computed: {
    pages: function () {
      return Math.ceil(this.total / 3)
    }
  },
  data () {
    return {
      position: 1
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
button{
  background-color: $dark-purple;
  color: $font-white;
  border: solid 1px $light-purple;
  border-radius: 25px;
  height: 35px;
  width: 35px;
  margin: 5px;
  font-family: $font-stack;
  font-weight: 100;
  cursor: pointer;
  vertical-align: middle;
}
button:hover, button:focus, button.selected{
  background-color: $light-purple;
}
.icon{
  vertical-align: middle;
}
.next-prev{
  background-color: $light-purple;
}
</style>



