<template>
  <div class="blog-list">
    <blog-post :title="blog.title" :age="parseDate(blog.updated)" :description="blog.content" v-for="blog in displayedBlogs" :key="blog.$index"></blog-post>
    <blog-pagination :total="this.blogs.length" @clicked="paginationClick"></blog-pagination>
  </div>
</template>

<script>
import BlogPost from './BlogPost'
import BlogPagination from './BlogPagination'
import api from '../../gateways/blogger'
import MomentTZ from 'moment-timezone'
export default {
  name: 'bloglist',
  components: {BlogPost, BlogPagination},
  methods: {
    parseDate: function (dateStr) {
      return MomentTZ.tz(dateStr, MomentTZ.tz.guess()).format('dddd, MMMM Do YYYY, h:mm a')
    },
    populateDisplay: function () {
      this.displayedBlogs = this.blogs.slice(this.offset, this.offset + 3)
    },
    paginationClick: function (newPosition) {
      this.offset = newPosition * 3
      this.populateDisplay()
    }
  },
  data () {
    return {
      offset: 0,
      displayedBlogs: [],
      blogs: []
    }
  },
  created: function () {
    api.getBlogPosts().then(function (results) {
      console.log(results.body.items)
      this.blogs = results.body.items
      this.populateDisplay()
    }.bind(this))
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
.blog-list{
  background-color: $dark-purple;
  padding: $home-section-padding;
}
</style>



