<template>
  <div class="social">
    <h2 class="section-header">Social Media</h2>
    <p>Check my social media links, follow me to stay updated</p>
    <a href="https://twitter.com/CatalystTwitch" title="twitter" target="_blank">
      <div class="link-wrap">
          <icon name="twitter" class="icon" :scale="iconSize"></icon>
          <label>Twitter</label>
          <span>{{twitterCount}} followers</span>
      </div>
    </a>
    <a href="https://discord.gg/XXEVjXu" title="discord" target="_blank">
      <div class="link-wrap">
          <icon name="discord" class="icon" :scale="iconSize"></icon>
          <label>Discord</label>
          <span>{{discordCount}} members</span>
      </div>
    </a>
    <a href="https://www.youtube.com/user/Lowco2525" title="youtube" target="_blank">
      <div class="link-wrap">
          <icon name="youtube" class="icon" :scale="iconSize"></icon>
          <label>Youtube</label>
          <span>{{youtubeCount}} subscribers</span>
      </div>
    </a>
    <a href="https://www.instagram.com/lowco2525/" title="instagram" target="_blank">
      <div class="link-wrap">
          <icon name="instagram" class="icon" :scale="iconSize"></icon>
          <label>Instagram</label>
          <span>{{instagramCount}} followers</span>
      </div>
    </a>
    <a href="https://www.twitch.tv/lowco" title="twitch" target="_blank">
      <div class="link-wrap">
          <icon name="twitch" class="icon" :scale="iconSize"></icon>
          <label>Twitch</label>
          <span>{{twitchCount}} followers</span>
      </div>
    </a>
  </div>
</template>

<script>
import SocialMediaIcons from '../header/SocialMediaIcons'
import twitter from '../../gateways/twitter'
import instagram from '../../gateways/instagram'
export default {
  name: 'twitch',
  components: {SocialMediaIcons},
  methods: {
    formatNumber: function (num) {
      return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
    }
  },
  created: function () {
    twitter.getUser().then(function (results) {
      this.twitterCount = this.formatNumber(results.body.followers_count)
    }.bind(this))
    instagram.getUserProfile().then(function (results) {
      console.log(results)
      // this.instagramCount = this.formatNumber(results.body.followers_count)
    })
    .catch(function (results) {
      console.log(results)
    })// .bind(this))
  },
  data () {
    return {
      iconSize: 3,
      twitterCount: '>10k',
      youtubeCount: '>7k',
      instagramCount: '>3k',
      discordCount: '>500',
      twitchCount: '>82k'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
  .social{
    padding: $home-section-padding;
  }
  .icon{
    display: block;
    fill: $light-purple;
    transition: $standard-transition;
    margin: 0 auto;
  }
  .link-wrap{
    display: inline-block;
    padding: 10px;
    text-align: center;
    border-radius: 20px;
    margin: 15px;
    transition: $standard-transition;
    label{
      color: $font-gray;
      transition: $standard-transition;
    }
    span{
      display: block;
      color: $light-purple;
      margin-top: 2px;
    }
    &:hover{
      background-color: $light-purple;
      transform: scale(1.1);
      label, span{
        color: $font-white;
      }
      .icon{
        fill: $font-white;
      }
    }
  }
</style>



