<template>
  <div class="stream-status">
    <p>Stream is <a href="https://www.twitch.tv/lowco" v-bind:class="{ online: streamStatus === 'ONLINE' }">{{streamStatus}}</a></p>
  </div>
</template>

<script>
import TwitchGateway from '../../gateways/twitch.js'
export default {
  name: 'stream-status',
  data () {
    return {
      streamStatus: 'OFFLINE'
    }
  },
  mounted () {
    TwitchGateway.isStreamLive(52925091).then(function (results) {
      if (results.body.stream !== null) {
        this.streamStatus = 'ONLINE'
      }
    }.bind(this))
  }
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';
a{
  display: inline-block;
  border: 2px solid $light-purple;
  border-radius: 25px;
  padding: 5px 20px;
  margin-left: 5px;
  color: $font-white;
  transition: $standard-transition;
  text-decoration: none;
  &.online{
    background-color: $light-purple;
    &:hover{
      transform: scale(1.05);
    }
  }
  &:hover{
    background-color: $light-purple;
  }
}
</style>



