<template>
  <div class="blog-post">
  	<h3 class="blog-title">{{title}}</h3>
  	<icon name="clock-o" class="icon"></icon><span>{{age}}</span>
  	<div v-html="styled" class="blog-clear" v-bind:class="{ expanded: isExpanded }"></div>
    <button v-on:click="expand" class="btn-expand">{{isExpanded ? 'Read Less' : 'Read More'}}</button>
  </div>
</template>

<script>
export default {
  name: 'blogpost',
  props: ['title', 'age', 'description'],
  data () {
    return {
      isExpanded: false
    }
  },
  methods: {
    expand: function () {
      this.isExpanded = !this.isExpanded
    }
  },
  computed: {
    styled: function () {
      var reg = /background-color.*?;|font-family.*?;/g
      return this.description.replace(reg, '')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
.icon{
  fill: $font-gray;
  vertical-align: bottom;
  margin-right: 5px;
}
.btn-expand{
  float: right;
  background-color: $light-purple;
  color: $font-white;
  border:none;
  border-radius: 25px;
  padding: 3px 15px;
  font-family: $font-stack;
  letter-spacing: 1px;
  font-weight: 100;
}
.blog-title{
  color: $dark-purple;
  font-size: 2.25em;
  margin: 0 0 5px 0;
}
.blog-post{
	background-color: $font-white;
  max-width: 700px;
  margin: 0 auto;
  border-radius: 15px;
  padding: 30px;
  transition: $standard-transition;
  margin: 0 auto 25px auto;
  padding: 40px 50px 55px 50px;
  text-align: left;
}
.blog-clear{
  text-align: initial;
  max-height: 350px;
  margin-top: 30px;
  overflow: hidden;
  transition: $blog-transition-close;
}
.expanded{
  max-height: 2000px;
  transition: $blog-transition-open;
}
</style>



